.App {
  text-align: center;
  margin: 50px auto;
  max-width: 400px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: aqua;
}

h1 {
  margin-bottom: 20px;
}

.input-container {
  margin-bottom: 10px;

}

label {
  display: block;
  margin-bottom: 5px;
}

input {
  width: 100%;
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.result-container {
  font-size: 18px;
  font-weight: bold;
  padding: 10px;
  background-color: black;
  border-radius: 4px;
color: #ccc;
}
